// importing all necessary files

import React, {Component} from "react";
import { useEffect, useState } from 'react';
import "./Main.css"
import Details from "./Details"
import "./Table.css"


const Table = (props) => {
  // create usestates that update the website when data, text search, popup button, or selected item are changed
  const [data, setData] = useState([]);
  const [searchText, setSearchText] = useState("");
  const [popupbutton, setPopupButton] = useState(false)
  const [selecteditem, setSelectedItem] = useState()
  
  useEffect( () => {
    // fetch the data from the EnergyStar url of the selected appliance and store as a json
    fetch(props.url,{
      headers : { 
        'Content-Type': 'application/json',
        'Accept': 'application/json'
       }
    }).then(result=>result.json()).then(json=>setData(json));
  }, [props.url]
  )
  if (data.length == 0) {
    return <></>;
  }
  // create a function that sets the search value equal to whatever the user types into search box
  function search(){
    const inputvalue = document.getElementById('searchvalue').value
    setSearchText(inputvalue);    
  }

  // create function that returns data with model number or brand similar to search value
  function filter(product){
    // const energy = props.option.energyfilter
    // console.log(props.url)
    if (product.model_number.toLowerCase().includes(searchText.toLowerCase())|| product.brand_name.toLowerCase().includes(searchText.toLowerCase()) || product[props.option.energyfilter].includes(searchText.toLowerCase())){
      return true
    }
  }
  
  // create function that opens the modal and sets the selected item to the clicked product
  function openPopup(product){
    setPopupButton(true)
    setSelectedItem(product)
    console.log(product)
  }



  // creating and rendering the page
  var headers =[]
  Object.keys(data[1]).map(x=>headers.push(x))
  return(
    <div>
      <div>
      </div>
      <h1>
        {props.appliance.toUpperCase()+"S"}
      </h1>
      {/* creating search box */}
      <input type = "text"  id = "searchvalue"placeholder = "search by model number or brand"/>
      <button type="submit" onClick = {search}><i className="fa fa-search">Search</i></button>
      {/* inserting modal popup, with props of selected item being passed down */}
      <Details trigger = {popupbutton} selecteditem = {selecteditem} onClose={()=>setPopupButton(false)} data = {data} openPopup = {openPopup} option = {props.option}/>
        <table id = {props.appliance} className = "table table-striped table-bordered table-hover table-sm" id = "tablepretty">
          <thead style={{position: "sticky", top: 0}} className = "sticky-header">
            <tr  id="headers">
              {headers.map(heading =><th className = "tableheading" style = {{backgroundColor:"rgba(255, 255, 255, 1)"}} key = {heading}>{heading.toUpperCase().replaceAll('_',' ')}</th>)              
               } 
            </tr>
          </thead>
          <tbody>

            {data.filter(filter).map( 
              product =><tr onClick = {()=>openPopup(product)}>{headers.map(header =><td className = "tablecell"><div className = "heightlimit">{" "+product[header]}</div></td>)}</tr>)              
              }
          </tbody>

        </table>
    </div>
  )
}
export default Table;