import React from "react";
import { useState } from "react";
import Table from "./Table"
import 'bootstrap/dist/css/bootstrap.min.css';
import DropdownButton from 'react-bootstrap/DropdownButton'
import Dropdown from 'react-bootstrap/Dropdown'


function Option(type, url, energyfilter=0, volumefilter=1){
    this.url = url
    this.type = type
    this.energyfilter = energyfilter
    this.volumefilter = volumefilter
}


const fridge = new Option("fridge","https://data.energystar.gov/resource/p5st-her9.json", "annual_energy_use_kwh_yr", "adjusted_volume_ft3")
const dryer = new Option("dryer","https://data.energystar.gov/resource/t9u7-4d2j.json","estimated_annual_energy_use_kwh_yr","width_inches")
const washer = new Option("washer", "https://data.energystar.gov/resource/bghd-e2wd.json","annual_energy_use_kwh_year", "volume_cubic_feet")
const dishwasher = new Option("dishwasher","https://data.energystar.gov/resource/58b3-559d.json", "annual_energy_use_kwh_year")
const roofing = new Option("roofing","https://data.energystar.gov/resource/gp4i-t95q.json",)
const boiler = new Option("boiler"," https://data.energystar.gov/resource/6rww-hpns.json","efficiency_afue")
const commercialwasher = new Option("commercial washer","https://data.energystar.gov/resource/9g6r-cpdt.json","annual_energy_use_kwh_year","volume_cubic_feet")
const efficientthermo = new Option("efficient thermostat","https://data.energystar.gov/resource/7p2p-wkbf.json")
const ventilatingfan= new Option("ventilating fan","https://data.energystar.gov/resource/8dv7-nngq.json")
const lightbulb = new Option("light bulb","https://data.energystar.gov/resource/ebgj-qsf7.json")
const lightfixture = new Option("light fixture","https://data.energystar.gov/resource/wyt9-72bp.json","fixture_type")
const waterheater= new Option("water heater","https://data.energystar.gov/resource/we7q-mgek.json","kwh_yr_at_125_f_outlet_temp")



const optionslist = [fridge, dryer, washer, dishwasher, roofing, boiler, commercialwasher, efficientthermo, ventilatingfan, lightbulb, lightfixture, waterheater]




const Optionscomponent = () => {
    const [Option, setOption] = useState(fridge)
    function onChangeValue(eventKey)
    {
        console.log(eventKey)
        var selectedoption
        for(let i =0; i < optionslist.length; i++){
            if (eventKey == optionslist[i].type)
            selectedoption = optionslist[i]
        }
        console.log(eventKey)
        // console.log(selectedoption.type)
        setOption(selectedoption)

    }
    return(
        <div>
            <DropdownButton onSelect = {onChangeValue} title = "Select Product Type">
            {optionslist.map(option => <><Dropdown.Item eventKey = {option.type} value = {option.type}> {option.type}</Dropdown.Item></>)
            }
            </DropdownButton>
            <div>
                <Table url ={Option.url} appliance ={Option.type} option = {Option}/>
            </div>
        </div>
      )
    }

export default Optionscomponent;
